


























































import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import '../definitions';

@Component
export default class Transactions extends Vue {
  boxId = this.$route.params.boxId;
  tab = this.getTabIndex(this.boxId);

  boxes: Box[] = [];


  getTabIndex(boxId?: BoxId) {
    if (!boxId) return 0;
    return this.$store.state.boxes.findIndex((box: Box) => box.id === boxId);
  }

  test() {
    if (this.$store.state.account)
      this.$store.dispatch("loadBoxes", { uid: this.$store.state.account.uid });
  }
}
